<style lang="scss" scoped>
  .btn_pay{
    width:100%;
    height:44px;
    line-height:42px;
    text-align:center;
    border-radius: 22px;
    display:inline-block;
    text-decoration: none;
    margin-top:20px;
  }
  .circle-left{
    top:118px;
    left:0;
  }
  .circle-right{
    top:118px;
    right:0;
  }
</style>
<template>
    <div class="device-width">
      <div class="parent-width parent-margin circular-bead box-shade pos-relate">
        <h2 class="text-center fs16 paddingT15 paddingB10"></h2>
        <div class="fs18 text-center paddingT10"></div>
        <div class="fs30 color-warn text-center paddingT10">{{this.cost_temp}}</div>
        <div class="item-height over-hidden marginT15 border-top-dash">
          <div class="parent-width parent-margin">
            <span class="font-bold"></span>
            <span class="fr"></span>
          </div>
        </div>
        <span class="half-circle-left circle-left pos-absolute bg-grey"></span>
        <span class="half-circle-right circle-right pos-absolute bg-grey"></span>
      </div>
      <div class="parent-width parent-margin">
        <a @click="pay"  class="fs20 bg-button color-white btn_pay"></a>
      </div>
    </div>
</template>

<script>
import NewWorkUtils from '../components/NetWrokUtils';
export default {
  data() {
    return {
      params: '',
      paymentId: '',
      cost_temp: '',
      wxRequest: '',
      payRequest: '',
      authentication: auth,
      orderid: ''
    }
  },

  created() {
  },
  mounted() {
    this.getPayInfo();
  },
  methods: {
    getPayInfo() {
      this.cost_temp = localStorage.getItem('pat_cost');
      this.orderid = localStorage.getItem('orderId');
      NewWorkUtils.post('/wx/baochuan_p/paymentorder', {
        authentication:this.authentication,
        orderid: this.orderid,
        cost: this.cost_temp
      }, (resp) => {
        this.paymentId = resp.data.content.paymentid;
        console.log(resp.data.content.paymentid);
      }, (error) => { })
    },
    pay() {
      if(this.$route.query.payWx == undefined || this.$route.query.payWx == ""){
        NewWorkUtils.post(
          '/wx/common_p/payment?paymentid=' + this.paymentId + '&authentication=' + this.authentication, {
            source: encodeURI(location.href)
          }, (resp) => {
            console.log(resp);
            window.location.href = resp.data.content;
          }, (error) => { })
      }else{
        this.payRequest = JSON.parse(decodeURI(this.$route.query.payWx));
        console.log(this.payRequest);
        if (typeof WeixinJSBridge == "undefined"){
          if( document.addEventListener ){
            document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
          }else if (document.attachEvent){
            document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
            document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
          }
        }else{
          WeixinJSBridge.invoke(
            'getBrandWCPayRequest',{
              "appId" : this.payRequest.appId,     //
              "timeStamp": this.payRequest.timeStamp,         //1970
              "nonceStr": this.payRequest.nonceStr, //
              "package" : this.payRequest.package,     //
              "signType": this.payRequest.signType,         //
              "paySign": this.payRequest.paySign //
            }
          )
        }
//        wx.config({
////          debug: false,
//          appId: this.payRequest.appid,
//          timestamp: this.payRequest.timeStamp, // 
//          nonceStr: this.payRequest.nonceStr, // 
//          signature: '',// 1
//          jsApiList: ['chooseWXPay'] //
//        });
//        wx.ready(function () {
//          wx.chooseWXPay({
//            timestamp: 0, // jssdktimestamptimeStampS
//            nonceStr: '', //  32 
//            package: '', // prepay_idprepay_id=***
//            signType: '', // 'SHA1''MD5'
//            paySign: '', // 
//            success: function (res) {
//              // 
//            }
//          });
//        })

      }
    }
  }
}
</script>



